const app = getApp({
	allowDefault: true
})
var QQMapWX = require('@/libs/qqmap-wx-jssdk.min.js')
var qqmapsdk = new QQMapWX({
	//key: 'LXCBZ-NNIKD-UZ64F-H6AFI-UNJLH-OCFGE'
	key: 'ZURBZ-WWTL6-ZNDSL-M7YHC-L67Q3-HRB7V'
})
export default {
	data() {
		return {
			detail: '',
			mapHeight: '',
			map: {
				longitude: 113.76927057974245,
				latitude: 34.76670519464811,
				showLocation: true,
				width: 40,
				height: 40,
				scale: 16,
				controls: []
			},
			list: [],
			oftenList: [],
			address: {
				title: '',
				address: ''
			},
			checked: 0,
			scrollTop: 0,
			mapStatus: 1, // 控制选择地址时 地图不加载附近列表
			mapCtx: '',
			Name: '',
		}
	},
	onNavigationBarButtonTap() {
		this.submit();
	},
	created() {
		uni.setNavigationBarTitle({
			title: '搜索地址'
		})
	},
	onLoad(option) {
		let map = this.map;
		if (option.name) {
			this.Name = option.name;
		}
		let that = this;
		uni.getLocation({
			type: 'gcj02', // 返回国测局坐标
			geocode: true,
			success: function(res) {
				that.map.latitude = res.latitude;
				that.map.longitude = res.longitude;
			},
			fail: function(e) {
				uni.showToast({
					icon: 'none',
					title: '获取地址失败, 请检查是否开启定位权限~~'
				})
			}
		});
		uni.getSystemInfo({
			success(res) {
				that.mapHeight = res.windowHeight - (res.windowWidth*120)/750 - 210 + 'px';
			}
		})
		// this.getWidthHeight(e => {
		// 	console.log(e);
		//   map.controls[0].position.top = e.height - 45
		//   map.controls[0].position.left = e.width/2 - 10
		//   this.setData({
		// 	map: map
		//   })
		// })

	},
	mounted() {
		let that = this
		uni.getSystemInfo({
			success(res) {
				that.mapHeight = res.windowHeight - (res.windowWidth*120)/750 - 210 + 'px';
			}
		})
	},
	methods: {
		getAddress() {
			let that = this
			uni.getLocation({
				type: 'gcj02',
				success: function(res) {
					console.log(res)
					let map = that.data.map
					map.longitude = res.longitude
					map.latitude = res.latitude
					that.getWidthHeight(e => {
						map.controls[0].position.top = e.height / 2 - 35
						map.controls[0].position.left = e.width / 2 - 20
						that.setData({
							map: map,
							position: {
								longitude: res.longitude,
								latitude: res.latitude
							}
						})
						that.getAddressList(1);
					})
				},
			})
		},
		getWidthHeight(fn) {
			var query = uni.createSelectorQuery()
			query.select('#map').boundingClientRect()
			query.exec(res => {
				fn(res[0])
			})
		},
		getAddressList(s = 0) {
			let that = this
			let position = that.position
			// console.log(position)
			qqmapsdk.reverseGeocoder({
				location: {
					latitude: position.latitude,
					longitude: position.longitude
				},
				get_poi: 1,
				poi_options: "page_size=20;page_index=1",
				success: function(e) {
					if (s) {
						e.result.pois[0].select = 1
						that.setData({
							list: e.result.pois,
							address: e.result.pois[0],
							checked: 0
						})
					} else {
						that.setData({
							list: e.result.pois
						})
					}
					setTimeout(() => {
						that.scrollTop = 1
					}, 1000)
				},
				fail: err => {
					console.log(err)
				}
			})
		},
		mapChange(e) {
			let that = this
			// console.log(this.mapStatus);
			clearTimeout(this.timer)
			this.timer = setTimeout(() => {
				if (e.type == 'regionchange' || e.type == 'end') {














					that.mapCtx = uni.createMapContext('map')
					that.mapCtx.getCenterLocation({
						success: res => {
							console.log(res.latitude)
							that.setData({
								position: {
									latitude: res.latitude,
									longitude: res.longitude,
								},
							})
							if (that.mapStatus) { // 防止地图点击时 进行多次加载
								that.getAddressList(1)
							} else {
								that.mapStatus = 1
							}
						}
					})

				}
			}, 200)
		},
		bindAddress(index) {
			let list = this.list
			let map = this.map
			map.latitude = list[index].location.lat
			map.longitude = list[index].location.lng
			this.setData({
				map: map,
				checked: index,
				address: list[index],
				mapStatus: 0
			})
			this.submit();
		},
		setData(obj) {
			Object.assign(this, obj)
		},
		addressSearch() {
			uni.navigateTo({
				url: '/pages/Home/MapSearch'
			})
		},
		submit() {
			let that = this
			let detail = that.detail || ''
			let address = that.address
			// console.log(address)
			let AddressData = {
				addressName: address.title + detail,
				address: address.address,
				lat: address.location.lat,
				lng: address.location.lng,
				adcode: address.ad_info.adcode,
				district: address.ad_info.district,
				city: address.ad_info.city,
			}
			if (this.Name == "qidian") {
				uni.setStorage({
					key: "StartPoint",
					data: AddressData,
					success() {
						uni.navigateBack({
							delta: 1
						})
					}
				})
			}
			if (this.Name == "zhongdian") {
				uni.setStorage({
					key: "EndPoint",
					data: AddressData,
					success() {
						uni.navigateBack({
							delta: 1
						})
					}
				})
			}
			// uni.navigateTo({
			// 	url:'../map/map',
			// 	success() {
			// 		uni.setStorage({
			// 			key:that.AddressData,
			// 			data:a,
			// 		})
			// 	}
			// })

			// console.log(a)
		}
	}
}
